  margin-right: 10px; }

.htFiltersConditionsMenu table tbody tr td div span.selected {
  margin-top: -2px;
  position: absolute;
  left: 4px; }

.htFiltersConditionsMenu .ht_master .wtHolder {
  overflow: hidden; }

.handsontable .htMenuFiltering {
  border-bottom: 1px dotted #ccc;
  height: 135px;
  overflow: hidden; }

.handsontable .ht_master table td.htCustomMenuRenderer {
  background-color: #fff;
  cursor: auto; }

/* Menu label */
.handsontable .htFiltersMenuLabel {
  font-size: 0.75em; }

/* Component action bar */
.handsontable .htFiltersMenuActionBar {
  text-align: center;
  padding-top: 10px;
  padding-bottom: 3px; }

/* Component filter by conditional */
.handsontable .htFiltersMenuCondition.border {
  border-bottom: 1px dotted #ccc !important; }

.handsontable .htFiltersMenuCondition .htUIInput {
  padding: 0 0 5px 0; }

/* Component filter by value */
.handsontable .htFiltersMenuValue {
  border-bottom: 1px dotted #ccc !important; }

.handsontable .htFiltersMenuValue .htUIMultipleSelectSearch {
  padding: 0; }

.handsontable .htFiltersMenuCondition .htUIInput input,
.handsontable .htFiltersMenuValue .htUIMultipleSelectSearch input {
  font-family: inherit;
  font-size: 0.75em;
  padding: 4px;
  box-sizing: border-box;
  width: 100%; }

.htUIMultipleSelect .ht_master .wtHolder {
  overflow-y: scroll; }

.handsontable .htFiltersActive .changeType {
  border: 1px solid #509272;
  color: #18804e;
  background-color: #d2e0d9; }

.handsontable .htUISelectAll {
  margin-right: 10px; }

.handsontable .htUIClearAll, .handsontable .htUISelectAll {
  display: inline-block; }

.handsontable .htUIClearAll a, .handsontable .htUISelectAll a {
  color: #3283D8;
  font-size: 0.75em; }

.handsontable .htUISelectionControls {
  text-align: right; }

.handsontable .htCheckboxRendererInput {
  margin: 0 5px 0 0;
  vertical-align: middle;
  height: 1em; }

/* UI elements */
/* Input */
.handsontable .htUIInput {
  padding: 3px 0 7px 0;
  position: relative;
  text-align: center; }

.handsontable .htUIInput input {
  border-radius: 2px;
  border: 1px solid #d2d1d1; }

.handsontable .htUIInput input:focus {
  outline: 0; }

.handsontable .htUIInputIcon {
  position: absolute; }

/* Button */
.handsontable .htUIInput.htUIButton {
  cursor: pointer;
  display: inline-block; }

.handsontable .htUIInput.htUIButton input {
  background-color: #eee;
  color: #000;
  cursor: pointer;
  font-family: inherit;
  font-size: 0.7em;
  font-weight: bold;
  height: 19px;
  min-width: 64px; }

.handsontable .htUIInput.htUIButton input:hover {
  border-color: #b9b9b9; }

.handsontable .htUIInput.htUIButtonOK {
  margin-right: 10px; }

.handsontable .htUIInput.htUIButtonOK input {
  background-color: #0f9d58;
  border-color: #18804e;
  color: #fff; }

.handsontable .htUIInput.htUIButtonOK input:hover {
  border-color: #1a6f46; }

/* Select */
.handsontable .htUISelect {
  cursor: pointer;
  margin-bottom: 7px;
  position: relative; }

.handsontable .htUISelectCaption {
  background-color: #e8e8e8;
  border-radius: 2px;
  border: 1px solid #d2d1d1;
  font-family: inherit;
  font-size: 0.7em;
  font-weight: bold;
  padding: 3px 20px 3px 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }

.handsontable .htUISelectCaption:hover {
  background-color: #e8e8e8;
  border: 1px solid #b9b9b9; }

.handsontable .htUISelectDropdown:after {
  content: '\25B2';
  font-size: 7px;
  position: absolute;
  right: 10px;
  top: 0; }

.handsontable .htUISelectDropdown:before {
  content: '\25BC';
  font-size: 7px;
  position: absolute;
  right: 10px;
  top: 8px; }

/* SelectMultiple */
.handsontable .htUIMultipleSelect .handsontable .htCore {
  border: none; }

.handsontable .htUIMultipleSelect .handsontable .htCore td:hover {
  background-color: #F5F5F5; }

.handsontable .htUIMultipleSelectSearch input {
  border-radius: 2px;
  border: 1px solid #d2d1d1;
  padding: 3px; }

.handsontable .htUIRadio {
  display: inline-block;
  margin-right: 5px;
  height: 100%; }

.handsontable .htUIRadio:last-child {
  margin-right: 0; }

.handsontable .htUIRadio > input[type=radio] {
  margin-right: 0.5ex; }

.handsontable .htUIRadio label {
  vertical-align: middle; }

.handsontable .htFiltersMenuOperators {
  padding-bottom: 5px; }

.handsontable.ganttChart tr:first-child th div.relative {
  padding-right: 21px; }

.handsontable.ganttChart .colHeader {
  display: block; }

.handsontable.ganttChart td.rangeBar {
  background: #48b703;
  border-right-width: 0;
  position: relative;
  -webkit-box-shadow: inset 0 3px 0 #ffffff;
  -moz-box-shadow: inset 0 3px 0 #ffffff;
  box-shadow: inset 0 3px 0 #ffffff; }

.handsontable.ganttChart td.rangeBar.last {
  border-right-width: 1px; }

.handsontable.ganttChart td.rangeBar.area {
  background: #7EC481; }

.handsontable.ganttChart td.rangeBar.partial {
  background: #8edf5a; }

.handsontable.ganttChart td.rangeBar.area.partial {
  background: #A1D8AD; }

.handsontable thead th.hiddenHeader:not(:first-of-type) {
  display: none; }

.handsontable th.ht_nestingLevels {
  text-align: left;
  padding-left: 7px; }

.handsontable th div.ht_nestingLevels {
  display: inline-block;
  position: absolute;
  left: 11px; }

.handsontable.innerBorderLeft th div.ht_nestingLevels,
.handsontable.innerBorderLeft ~ .handsontable th div.ht_nestingLevels {
  right: 10px; }

.handsontable th span.ht_nestingLevel {
  display: inline-block; }

.handsontable th span.ht_nestingLevel_empty {
  display: inline-block;
  width: 10px;
  height: 1px;
  float: left; }

.handsontable th span.ht_nestingLevel::after {
  content: "\2510";
  font-size: 9px;
  display: inline-block;
  position: relative;
  bottom: 3px; }

.handsontable th div.ht_nestingButton {
  display: inline-block;
  position: absolute;
  right: -2px;
  cursor: pointer; }

.handsontable th div.ht_nestingButton.ht_nestingExpand::after {
  content: "\002B"; }

.handsontable th div.ht_nestingButton.ht_nestingCollapse::after {
  content: "\002D"; }

.handsontable.innerBorderLeft th div.ht_nestingButton,
.handsontable.innerBorderLeft ~ .handsontable th div.ht_nestingButton {
  right: 0; }

/*
 * Handsontable HiddenColumns
 */
.handsontable th.beforeHiddenColumn {
  position: relative; }

.handsontable th.beforeHiddenColumn::after,
.handsontable th.afterHiddenColumn::before {
  color: #bbb;
  position: absolute;
  top: 50%;
  font-size: 5pt;
  transform: translateY(-50%); }

.handsontable th.afterHiddenColumn {
  position: relative; }

.handsontable th.beforeHiddenColumn::after {
  right: 1px;
  content: '\25C0'; }

.handsontable th.afterHiddenColumn::before {
  left: 1px;
  content: '\25B6'; }

.handsontable td.firstVisibleColumn,
.handsontable th.firstVisibleColumn {
  border-left: 1px solid #CCC; }

/*!
 * Handsontable HiddenRows
 */
.handsontable th.beforeHiddenRow::before,
.handsontable th.afterHiddenRow::after {
  color: #bbb;
  font-size: 6pt;
  line-height: 6pt;
  position: absolute;
  left: 2px; }

.handsontable th.beforeHiddenRow,
.handsontable th.afterHiddenRow {
  position: relative; }

.handsontable th.beforeHiddenRow::before {
  content: '\25B2';
  bottom: 2px; }

.handsontable th.afterHiddenRow::after {
  content: '\25BC';
  top: 2px; }

.handsontable.ht__selection--rows tbody th.beforeHiddenRow.ht__highlight:before,
.handsontable.ht__selection--rows tbody th.afterHiddenRow.ht__highlight:after {
  color: #eee; }

.handsontable td.afterHiddenRow.firstVisibleRow,
.handsontable th.afterHiddenRow.firstVisibleRow {
  border-top: 1px solid #CCC; }
